.MargenTitle {
    font-size: 30px;
    color: rgb(12, 20, 59);
    cursor: vertical-text;
    padding-left: 5em;
    margin-top: 0.42em;
}

.separator {
    width: 1px;
    height: 4em;
    background-color: #2476FA;
    margin-top: 2em;
}

.WorkCard {
    display: flex;
}

.ExperienceContainer {
    display: flex;
    padding-top: 1.5em;
    flex-direction: column;
    flex: 1;
}

.SkillsItems {
    display: grid;
    grid-template-columns: auto auto;
    grid-gap: 0rem;
    margin: 0;
    padding: 4em 7em 1em 3em;
    list-style: none;
}

.descriptionWork {
    font-size: 16px;
    margin-right: 6em;
    margin-top: 3em;
    color: #4C5544;
}

.SkillItem {
  color: #2476FA;
  font-weight: 600;
}

.SkillsContainer {
    display: flex;
    flex: 1;
    flex-direction: column;
}

.ListItems {
    padding-left: 1.8em;
    padding-right: 3em;
    list-style: none;
}

.companyLogo {
    width: 121px;
    height: 121px;
    margin-left: 3em;
}

.CompanyContainer {
    display: flex;
}

.Item {
    color: #4C5544;
}

.Item::before {
    content: "•"; 
    font-size: 20px;
    line-height: 0em;
    color: #2476FA;
    display: inline-block;
    width: 1em;
    margin-left: -1em
}

.NavContainer {
   padding-bottom: 2em;
}

.experienceSubtitle {
    font-size: 18px;
    color: #8F95A3;
    line-height: 0px;
    margin-top: 0.3em;
}

.experienceTitle {
    font-size: 30px;
    line-height: 0px;
}

.Slash {
    color: #2476FA;
    font-size: 15px;
}

.CurrentRoute {
    color: rgb(4, 24, 17);
}

.Content {
    position: absolute;
    padding-top: 7.8em;
    top: 0px;
    display: flex;
    flex: 1;
    width: 85vw;
    flex-direction: column;
    padding-left: 10em;
}

.CompanyName {
    font-size: 30px;
    margin-left: 1em;
}

.TeamName {
    margin-left: 1.5em;
    font-size: 20px;
    margin-top: -2em; 
    color: #8F95A3;
}

.CompanyNameTwo {
    font-size: 16px;
}

.RouteTextOne:hover {
    text-decoration: underline;
    color: #2476FA;
}

.RouteTextOne {
    color: #8F95A3;
    text-decoration: none;
}

.NavText {
    font-size: 16px;
}

.container {
    z-index: -1;
    position: relative;
}

.PageColorSeparator {
    width: 8em;
    height: 2px;
    margin-left: 2em;
    background-color: #2476FA;
    margin-top: 4em;
}

.PageSeparator {
    width: 100vw;
    margin-left: -10em; 
    height: 1px;
    margin-bottom: 4em;
    background-color: #E5E5E5;
}

.MargenContainer {
    padding: 0px;
    transform: rotate(90deg);
    transform-origin: top left;
    margin-left: 5rem;
    z-index: 900;
    position: relative;
    height: 100vh;
    border-top: 1px solid #E5E5E5;
}

@media (max-width: 1220px) {
    .WorkCard {
        flex-direction: column;
    }
    .ExperienceContainer {
        margin-bottom: 3em;
        padding-right: 2em;
    }
}

@media (max-width: 950px) {
    .companyLogo {
        width: 90px;
        height: 90px;
        margin-left: 2em;
        margin-top:1.3em;
    }
}

@media (max-width: 680px) {
    .companyLogo {
        width: 60px;
        height: 60px;
        margin-left: 2em;
        margin-top: 2.1em;
    }
    .CompanyName {
        font-size: 20px;
        margin-left: 1em;
        margin-top: 2em;
    }
    
    .TeamName {
        margin-left: 1.5em;
        font-size: 13px;
        margin-top: -2em; 
        color: #8F95A3;
    }
}

@media (max-width: 650px) {
    .MargenContainer {
        display: none;
    }

    .companyLogo {
        width: 60px;
        height: 60px;
        margin-left: 1em;
        margin-top: 2.1em;
    }

    .separator {
        width: 1px;
        height: 4em;
        background-color: #2476FA;
        margin-top: 2em;
        display: inline-block;
    }

    .Content {
        padding-left: 6em;
    }

    .descriptionWork {
        font-size: 16px;
        margin-right: 6em;
        margin-top: 3em;
        margin-left: 1em;
        color: #4C5544;
    }
}

@media (max-width: 640px) {
    .SkillsItems {
        display: grid;
        grid-template-columns: auto;
        grid-gap: 0rem;
        margin: 0;
        padding: 4em 7em 1em 3em;
        list-style: none;
    }
    .CompanyContainer {
        /* flex-direction: column; */
    }
    .separator {
        width: 1px;
        height: 4em;
        background-color: #2476FA;
        margin-top: 2em;
    }
    /* .CompanyName {
        font-size: 20px;
        margin-left: 1em;
    }
    .TeamName {
        margin-left: 1.5em;
        font-size: 15px;
        margin-top: -2em; 
        color: #8F95A3;
    } */
}

@media (max-width: 510) {
    .Content {
        padding-left: 3em;
    }
    .CompanyName {
        font-size: 14px;
        margin-left: 1em;
        margin-top: 2em;
    }
    
    .TeamName {
        margin-left: 1.5em;
        font-size: 9px;
        margin-top: -2em; 
        color: #8F95A3;
    }
    .companyLogo {
        width: 40px;
        height: 40px;
        margin-left: 1em;
        margin-top: 2.8em;
    }
}

@media (max-width: 480px) {
    .Content {
        padding-left: 2.5em;
    }

    .experienceTitle {
        font-size: 25px;
    }

    .ExperienceContainer {
        padding-right: 0em;
    }
}